/* ibm-plex-sans-300 - cyrillic */
@font-face {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 300;
  src: local("IBM Plex Sans Light"), local("IBMPlexSans-Light"),
    url("../fonts/ibm-plex-sans-v8-cyrillic-300.woff2") format("woff2"),
    url("../fonts/ibm-plex-sans-v8-cyrillic-300.woff") format("woff");
}

/* ibm-plex-sans-regular - cyrillic */
@font-face {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  src: local("IBM Plex Sans"), local("IBMPlexSans"),
    url("../fonts/ibm-plex-sans-v8-cyrillic-regular.woff2") format("woff2"),
    url("../fonts/ibm-plex-sans-v8-cyrillic-regular.woff") format("woff");
}

